import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-register-form',
  templateUrl: './register-form.component.html',
  styleUrls: ['./register-form.component.scss']
})
export class RegisterFormComponent implements OnInit {

  phoneNumber: FormGroup;
  otp: FormGroup;
  password: FormGroup;
  phone_step = true;
  otp_step = false;
  password_login_step = false;
  trouble_login_step = false;
  newsbox_login_step = false;
  register_newsbox1_step = false;
  register_newsbox2_step = false;
  register_newsbox3_step = false;
  register_user1_step = false;
  register_user2_step = false;
  register_user3_step = false;
  step = 1;

  constructor(private fb: FormBuilder) { }

  get phone() { return this.phoneNumber.controls; }
  get passwordC() { return this.password.controls; }
  get otpC() { return this.otp.controls; }

  phoneSubmitted($event){
    this.otp = $event;
    if (this.otp.invalid) { return  }
    this.step++
  }

  otpSubmitted($event){
    this.phoneNumber = $event;
    if (this.phoneNumber.invalid) { return  }
    this.step++
  }

  passwordSubmitted($event){
    this.password = $event;
  }

  nextScreen($event){
    if($event == "otp"){
      this.otp_step = true;
      this.phone_step = false;
    }
    if($event == "passwordLogin"){
      this.password_login_step = true;
      this.otp_step = false;
    }
    if($event == "home"){
      this.password_login_step = false;
    }
    if($event == "trouble"){
      this.phone_step = false;
      this.password_login_step = false;
      this.trouble_login_step = true;
    }if($event == "phone"){
      this.phone_step = true;
      this.trouble_login_step = false;
    }
    if($event == "newsboxLogin"){
      this.newsbox_login_step = true;
      this.phone_step = false;
    }
    if($event == "registerNewsBox1"){
      this.newsbox_login_step = false;
      this.register_newsbox1_step = true;
    }
    if($event == "registerNewsBox2"){
      this.register_newsbox1_step = false;
      this.register_newsbox2_step = true;
    }
    if($event == "registerNewsBox3"){
      this.register_newsbox2_step = false;
      this.register_newsbox3_step = true;
    }
    if($event == "registerUser2"){
      this.register_user1_step = false;
      this.register_user2_step = true;
    }
    if($event == "registerUser3"){
      this.register_user2_step = false;
      this.register_user3_step = true;
    }
  }

  next(){
    if(this.step==1){
          this.phone_step = true;
          if (this.phoneNumber.invalid) { return  }
          this.step++
    }
    if(this.step==2){
        this.otp_step = true;
        if (this.otp.invalid) { return }
            this.step++;
    }
  }

  previous(){
    this.step--
    if(this.step==1){
      this.phone_step = false;
    }
    if(this.step==2){
      this.password_login_step = false;
    }
  }

  submit(){
    if(this.step==3){
      this.password_login_step = true;
      if (this.password.invalid) { return }
    }
  }

  ngOnInit() {
    this.phoneNumber = this.fb.group({
      phone: ['', Validators.required]
    });
    this.otp = this.fb.group({
        otp: ['', Validators.required],
    });
    this.password = this.fb.group({
        password: ['', Validators.required]
    });
  }

}
