import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-newsbox-login',
  templateUrl: './newsbox-login.component.html',
  styleUrls: ['./newsbox-login.component.css']
})
export class NewsboxLoginComponent implements OnInit {

  user: FormGroup;
  @Output() nextScreen: EventEmitter<string> = new EventEmitter<string>();


  constructor(private fb: FormBuilder) { }

  onSubmit(){
    this.nextScreen.emit("home");
  }

  registerNewsBox(){
    this.nextScreen.emit("registerNewsBox1");
  }

  ngOnInit() {
    this.user = this.fb.group({
      email: ['', Validators.required],
      password: ['', Validators.required]
    });
  }

}
