import { Component, Input, OnInit, Output } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { EventEmitter } from '@angular/core';
import { NumberSymbol } from '@angular/common';

@Component({
  selector: 'app-otp',
  templateUrl: './otp.component.html',
  styleUrls: ['./otp.component.css']
})
export class OtpComponent implements OnInit {
  @Input() phoneNumber: any;

  @Output() onFormGroupChange: EventEmitter<FormGroup> = new EventEmitter<FormGroup>();
  @Output() nextScreen: EventEmitter<string> = new EventEmitter<string>();
  
  otp: FormGroup;

  get otp2() {
    return this.otp.controls;
  }

  constructor(private fb: FormBuilder) { 
    
  }

  submit(){
    const inputs = <NodeListOf<HTMLInputElement>>document.querySelectorAll('#otp > *[id]');
    var otp3 = '';
    for (let i = 0; i < inputs.length; i++) {
      otp3 += inputs[i].value; 
    }
    if(otp3.length == 4){
      this.otp2.otp.setValue(otp3);
      this.onFormGroupChange.emit(this.otp);
      this.nextScreen.emit("home");
    }
  }

  passwordLogin(){
    this.nextScreen.emit("passwordLogin");
  }

  ngOnInit() {

    this.otp = this.fb.group({
      otp: ['', Validators.required]
    });

    const inputs = <NodeListOf<HTMLInputElement>>document.querySelectorAll('#otp > *[id]');
    for (let i = 0; i < inputs.length; i++) {
      inputs[i].addEventListener('keydown', function(event: KeyboardEvent) {
        if (event.key === "Backspace") {
          inputs[i].value = '';
          if (i !== 0)
            inputs[i - 1].focus();
        } else {
          if (i === inputs.length - 1 && inputs[i].value !== '') {
            return true;
          } else if (event.keyCode > 47 && event.keyCode < 58) {
            inputs[i].value = event.key;
            if (i !== inputs.length - 1)
              inputs[i + 1].focus();
            event.preventDefault();
          } else if (event.keyCode > 64 && event.keyCode < 91) {
            inputs[i].value = String.fromCharCode(event.keyCode);
            if (i !== inputs.length - 1)
              inputs[i + 1].focus();
            event.preventDefault();
          }
        }
      });
    }


  }

}
