import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-newsbox-register2',
  templateUrl: './newsbox-register2.component.html',
  styleUrls: ['./newsbox-register2.component.css']
})
export class NewsboxRegister2Component implements OnInit {

  nb2: FormGroup;
  @Output() nextScreen: EventEmitter<string> = new EventEmitter<string>();

  constructor(private fb: FormBuilder) { }

  onSubmit(){
    this.nextScreen.emit("registerNewsBox3");
  }

  ngOnInit() {
    this.nb2 = this.fb.group({
      logo: ['', Validators.required],
      page: ['', Validators.required],
      email: ['', Validators.required],
      url1: ['', Validators.required],
      url2: ['', Validators.required],
      url3: ['', Validators.required]
    });
  }

}
