import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-password-login',
  templateUrl: './password-login.component.html',
  styleUrls: ['./password-login.component.css']
})
export class PasswordLoginComponent implements OnInit {

  @Input() phoneNumber: any;

  @Output() onFormGroupChange: EventEmitter<FormGroup> = new EventEmitter<FormGroup>();
  @Output() nextScreen: EventEmitter<string> = new EventEmitter<string>();

  password: FormGroup;
  
  constructor(private fb: FormBuilder) { }

  onSubmit(){
    this.onFormGroupChange.emit(this.password);
    this.nextScreen.emit("home");
  }

  trouble(){
    this.nextScreen.emit("trouble");
  }

  ngOnInit() {
    this.password = this.fb.group({
      password: ['', Validators.required]
    });
  }
}
