import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-newsbox-register1',
  templateUrl: './newsbox-register1.component.html',
  styleUrls: ['./newsbox-register1.component.css']
})
export class NewsboxRegister1Component implements OnInit {


  nb1: FormGroup;
  @Output() nextScreen: EventEmitter<string> = new EventEmitter<string>();

  constructor(private fb: FormBuilder) { }

  onSubmit(){
    this.nextScreen.emit("registerNewsBox2");
  }

  ngOnInit() {
    this.nb1 = this.fb.group({
      email: ['', Validators.required],
      password: ['', Validators.required]
    });
  }

}
