import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-user-register1',
  templateUrl: './user-register1.component.html',
  styleUrls: ['./user-register1.component.css']
})
export class UserRegister1Component implements OnInit {

  @Output() onFormGroupChange: EventEmitter<FormGroup> = new EventEmitter<FormGroup>();
  @Output() nextScreen: EventEmitter<string> = new EventEmitter<string>();
  
  user1: FormGroup;
  constructor(private fb: FormBuilder) { }

  onSubmit(){
    this.nextScreen.emit("userRegister2");
  }

  ngOnInit() {
    this.user1 = this.fb.group({
      username: ['', Validators.required]
    });
  }

}
