import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-trouble-login',
  templateUrl: './trouble-login.component.html',
  styleUrls: ['./trouble-login.component.css']
})
export class TroubleLoginComponent implements OnInit {

  username: FormGroup;
  @Output() nextScreen: EventEmitter<string> = new EventEmitter<string>();

  
  constructor(private fb: FormBuilder) { }

  onSubmit(){
    this.nextScreen.emit("phone");
  }

  ngOnInit() {
    this.username = this.fb.group({
      username: ['', Validators.required]
    });
  }
}
